"use strict";
import { initEmblaFromElements } from "./embla-core";
export default function initEmblaSlider(containerClass, options = { loop: true }, autoplayOptions = { active: false }) {
  const containers = Array.from(document.querySelectorAll(`${containerClass}`));
  const teardowns = [];
  containers.forEach((container) => {
    const emblaNode = container.querySelector(".embla");
    if (!emblaNode) return;
    const viewportNode = emblaNode.querySelector(".embla__viewport");
    const prevBtnNode = emblaNode.querySelector(".embla__button--prev");
    const nextBtnNode = emblaNode.querySelector(".embla__button--next");
    if (!viewportNode) return;
    const initResult = initEmblaFromElements(options, {
      viewport: viewportNode,
      prevBtn: prevBtnNode != null ? prevBtnNode : null,
      nextBtn: nextBtnNode != null ? nextBtnNode : null
    }, autoplayOptions);
    const observer = new MutationObserver((mutations) => {
      for (const m of mutations) {
        for (const removedNode of Array.from(m.removedNodes)) {
          if (removedNode === container || removedNode instanceof HTMLElement && removedNode.contains(container)) {
            initResult.destroy();
            observer.disconnect();
            return;
          }
        }
      }
    });
    observer.observe(document.body, { childList: true, subtree: true });
    teardowns.push(() => {
      observer.disconnect();
      initResult.destroy();
    });
  });
  return teardowns;
}
